.timer{position:absolute;display:flex;align-items:center;justify-content:center;gap:1rem;bottom:1rem;width:100%;margin:0 auto}@media screen and (max-width: 1024px){.timer{gap:.5rem}}.timer span{color:var(--brown-deep-color);font-family:LibreCaslon,serif;font-style:italic;font-size:8rem;font-weight:var(--display-weight);line-height:1;letter-spacing:var(--display-spacing);opacity:.25}@media screen and (max-width: 1024px){.timer span{font-size:10vw}}