.fab{position:fixed;z-index:999;display:flex;align-items:center;bottom:1rem;left:50%;padding:1rem;background:rgba(var(--black-rgb), 0.675);border-radius:.75rem;transform:translate3d(-50%, 200%, 0);opacity:0;transition:transform .6s ease,opacity .6s ease;-webkit-backdrop-filter:blur(0.25rem);backdrop-filter:blur(0.25rem)}@media screen and (max-width: 768px){.fab{padding:.75rem}}.fab span{padding:.5rem .675rem;color:var(--white-color);border-radius:.25rem;line-height:1}@media screen and (max-width: 768px){.fab span{padding:.5rem}}.fab span:nth-of-type(1){font-size:var(--label-size);font-weight:var(--label-weight);letter-spacing:var(--label-spacing)}@media screen and (max-width: 320px){.fab span:nth-of-type(1){display:none}}.fab span:nth-of-type(1) strong{font-family:LibreCaslon,serif;font-style:italic;font-weight:600}.fab span:not(:nth-of-type(1)){font-size:var(--subtext-size);font-weight:var(--subtext-weight);letter-spacing:var(--subtext-spacing);opacity:.5}.fab span:not(:nth-of-type(1)):hover{text-decoration:underline;text-underline-offset:.25em;opacity:1}.fab.-visible{transform:translate3d(-50%, 0, 0);opacity:1}